import React from 'react';

import Card from '../components/Card';

import devgrub from '../assets/images/githublogo3.png';
import youtube from '../assets/images/bodya.jpg';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';

class Carousel extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            items: [
                {
                   
                    id: 0,
                    title: 'Bogdan Kovalov',
                    subTitle: 'Instagram',
                    imgSrc: youtube,
                    link: 'https://www.instagram.com/bodya_kovalov/',
                    selected: false
                   
                },
                {
                    id: 1,
                    title: 'My Portfolio',
                    subTitle: 'Finished Work',
                    imgSrc: devgrub,
                    link: 'https://github.com/bogdankvlv?tab=repositories',
                    selected: false
                },
                
            ]
        }
    }


    handleCardClick = (id, card) => {

        let items = [...this.state.items];

        items[id].selected = items[id].selected ? false : true;

        items.forEach(item => {
            if(item.id !== id) {
                item.selected = false;
            }
        });

        this.setState({
            items
        });
    }


    makeItems = (items) => {
        return items.map(item => {
            return <Card item={item} click={(e => this.handleCardClick(item.id, e))} key={item.id} />
        })
    }


    render() {
        return(
            <Container fluid={true}>
                <Row className="justify-content-around">
                    {this.makeItems(this.state.items)}
                </Row>
            </Container>
        );
    }

}

export default Carousel;